@import "../../styles/colors.scss";

.IframeContainer {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow-y: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0990f0a6;
  &::-webkit-scrollbar {
    width: 0;
  }

  > a {
    background-color: $withe-color;
    border-radius: 20px;
    box-shadow: -2px 3px 8px 5px #0A8FEC30;
    color: $blue-light;
    font-weight: 700;
    left: 20px;
    padding: 15px 25px;
    position: absolute;
    text-decoration: none;
    top: 20px;
    transition: all ease .3s;
    z-index: 999;
    font-size: 20px;
    &:hover {
        color: $withe-color;
        background-color: $blue-light;
    }
    .Icon {
      margin-right: 7px;
      
    }
  }

  > iframe {
    width: 82%;
    height: 97vh;
    border-radius: 15px;
  }
}
