@import "../../styles/colors.scss";

.adminLayoud {
    background-image: url('../../assets/img/BG-admin.svg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 90px 0 0;
    width: 100%;

    &__Content {
        margin-top: 15px;
    }
}

@media screen and (min-width: 1220px ) {
       .adminLayoud {
        display: flex;
        padding: 0;
        align-items: flex-end;
         &__Content {
            height: 100vh;
            overflow-y: scroll;
            padding-top: 10vh;
            width: 85%;
            margin: 0;
         }
       } 
}
